.hire-system-section {
    overflow: hidden;
    width: 100%;
    height: 438px;

    .hire-system-content {
        height: 100%;
        border-top: 94px solid transparent;

        .intro {
            font-size: 20px;
            font-weight: 400;
            margin-bottom: 50px;
        }

        .line {
            border-radius: 10px;
            width: 80%;
            height: 10px;
            background-color: #E4E4E4;
            margin: auto;

            .send-point {
                position: relative;
                width: 5%;               

                .point {
                    display: inline-block;
                    position: absolute;
                    top: -10px;

                    .point-content {
                        position: relative;

                        .intro {
                            position: absolute;
                            text-align: left;
                            width: 200px;
                            left: -10px;
                            top: 70px;
                            font-weight: 400;
                            font-size: larger;
        
                            .description {
                                font-weight: normal;
                                font-size: 15px;
                                padding-top: 10px;
                            }
                        }
                    }
                }
            }

            .check-point {
                position: relative;
                width: 60%;

                .point {
                    display: inline-block;
                    position: absolute;
                    top: -10px;

                    .point-content {
                        position: relative;

                        .intro {
                            position: absolute;
                            text-align: left;
                            width: 200px;
                            left: -10px;
                            top: 70px;
                            font-weight: 400;
                            font-size: larger;
        
                            .description {
                                font-weight: normal;
                                font-size: 15px;
                                padding-top: 10px;
                            }
                        }
                    }
                }
            }

            .answer-point {
                position: relative;
                width: 140%;

                .point {
                    display: inline-block;
                    position: absolute;
                    top: -10px;

                    .point-content {
                        position: relative;

                        .intro {
                            position: absolute;
                            text-align: left;
                            width: 200px;
                            left: -10px;
                            top: 70px;
                            font-weight: 400;
                            font-size: larger;
        
                            .description {
                                font-weight: normal;
                                font-size: 15px;
                                padding-top: 10px;
                            }
                        }
                    }
                }
            }

            .finish-point {
                position: relative;
                width: 199%;

                svg {
                    position: absolute;
                    top: -10px;                    
                }
            }
        }
    }
}

.mail-icon {
    width: 30px;
    .st0{fill:#4654aa;}
    .st1{opacity:0.2;}
    .st2{fill:#231F20;}
    .st3{fill:#E0E0D1;}
    .st4{fill:#FFFFFF;}
}
